<template>
    <div class="az-footer">
        <div class="az-socail">
            <div class="az-footer-social-box">
                <a href="mailto:albertaz1992@gmail.com" target="_blank" >
                    <az-icon name="envelope-o" scale="1" class="az-footer-social-icon"></az-icon>
                </a>
            </div>
            <div class="az-footer-social-box">
                <a href="https://www.twitter.com/Az1992Albert" target="_blank">
                    <az-icon name="twitter" scale="1" class="az-footer-social-icon"></az-icon>
                </a>
            </div>
            <div class="az-footer-social-box">
                <a href="https://www.instagram.com/albertaz1992" target="_blank">
                    <az-icon name="instagram" scale="1" class="az-footer-social-icon"></az-icon>
                </a>
            </div>
            <div class="az-footer-social-box">
                <a href="https://github.com/AlbertAZ1992" target="_blank">
                    <az-icon name="github" scale="1" class="az-footer-social-icon"></az-icon>
                </a>
            </div>

        </div>
        <div class="az-copy-right">
            <p>All work © Albert Zhang 2018</p>
        </div>

    </div>
</template>
<script>
import { LINKS } from '@/common/config';
import Icon from 'vue-awesome/components/Icon';

export default {
    name: 'az-footer',
    props: ['showHeaderClose'],
    components: {
        'az-icon': Icon,
    },
    data() {
        return {
            links: LINKS,
            isDark: false,
        };
    },
    mounted() {
        window.onscroll = () => {
            this.isDark = document.documentElement.scrollTop > window.innerHeight;
        };
    },
    methods: {
        showMenu() {
            this.$emit('showMenu');
        },
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';
.az-footer {
    .az-socail {
        width: @width-base * 2;
        margin: @margin-base auto;
        display: flex;
        justify-content: space-around;
        .az-footer-social-box {
            flex: 0 1 auto;
            width: 30px;
            height: 30px;
            display: inline-block;
            border-radius: 50%;
            font-size: 20px;
            background: @gray-light-color;
            box-shadow: 0px 0px 5px @gray-light-color;
            .az-footer-social-icon {
                color: @white-color;
                margin: 5px;
                height: 20px;
                width: 20px;
            }
            &:hover {
                cursor: pointer;
                background: @az-light-golden-color;
                // .az-footer-social-icon {
                //     color: @az-light-golden-color;
                // }
            }
        }

    }
    .az-copy-right {
        margin: @margin-base auto;
        p {
            text-align: center;
            font-size: @font-size-base;
            color: @black-color;
        }
    }
}


</style>



