<template>
    <div class="az-works">
        <div class="az-works-illustration">
            <div v-for="(item, index) in illustrations"
                :class="`work-${index}`"
                class="work"
                :style="{background: item.bgColor}">
                <router-link :to="item.route">
                    <p class="title">{{item.title}}</p>
                    <p class="sub">{{item.sub}}</p>
                </router-link>
                <div class="mask"></div>
                <img class="screen-shot" :src="item.url">
            </div>
        </div>
    </div>
</template>
<script>
import { ILLUSTRATIONS } from '@/common/config';

export default {
    name: 'az-works',
    data() {
        return {
            illustrations: ILLUSTRATIONS,
            height: 'auto',
        };
    },
};
</script>
<style lang="less">
@import '../common/css/main.less';
.az-works {
    h3 {
        margin: @margin-base * 2;
        color: @black-color;
    }
    .az-works-illustration {
        display: flex;
        flex-wrap: wrap;
        margin: @margin-base * 2 0 @margin-base * 2 @margin-base * 2;
        .work {
            flex: 1 1 auto;
            display: flex;
            position: relative;
            width: @width-base * 3;
            height: @width-base * 3;
            margin: 0 @margin-base * 2 @margin-base * 2 0;
            box-shadow: 0px 0px 10px @gray-light-color;
            overflow: hidden;
            justify-content: center;
            align-items: center;
            p {
                position: absolute;
                color: @white-color;
                z-index: 10;
            }
            .title {
                margin: 0;
                opacity: 0;
                font-size: 3rem;
                color: @az-light-golden-color;
                top: 30%;
                left: 10%;
                &:before {
                    position: absolute;
                    content: '';
                    display: block;
                    height: 7px;
                    width: 0;
                    top: 50%;
                    transform: translate3d(-1%, 0, 0) scaleX(1);
                    background: @white-color;
                    transition: width .3s ease, transform .3s ease-out .3s;
                    transform-origin: right center;
                }
            }
            .sub {
                margin: 0;
                opacity: 0;
                color: @az-light-golden-color;
                font-size: 1rem;
                top: calc(~"30% + 4rem");
                left: 10%;
            }
            .mask {
                position: absolute;
                background: @black-color;
                width: 100%;
                height: 100%;
                opacity: 0;
                z-index: 9;
            }
            .screen-shot {
                max-width: calc(~"90% - 1rem");
                max-height: calc(~"90% - 1rem");
            }
            &:hover {
                cursor: pointer;
                .mask {
                    opacity: 0.6;
                }
                .title {
                    opacity: 1;
                    &:before {
                        width: 102%;
                        transform: translate3d(-1%, 0, 0) scaleX(0);
                    }
                }
                .sub {
                    opacity: 1;
                }
            }

        }
    }
}
@media only screen and (max-width:@screen-sm) {

}

</style>



