<template>
    <div id="app" class="app-layout" :class="{'noScroll': showMenu}"
        :style="{height: currentHeight}">
        <az-header @showMenu="broadcastShowMenu" @hideMenu="broadcastHideMenu"/>
        <transition name="">
            <router-view/>
        </transition>
        <az-footer/>
    </div>
</template>

<script>
    import Header from '@/components/Header';
    import Footer from '@/components/Footer';

    export default {
        name: 'app',
        components: {
            'az-header': Header,
            'az-footer': Footer,
        },
        data() {
            return {
                showMenu: false,
                currentHeight: 'auto',
            };
        },
        methods: {
            broadcastShowMenu() {
                this.showMenu = true;
                this.currentHeight = `${window.innerHeight}px`;
            },
            broadcastHideMenu() {
                this.showMenu = false;
                this.currentHeight = 'auto';
            },
        },
    };
</script>

<style lang="less">
@import './common/css/main.less';
.app-layout.noScroll {
  overflow: hidden;
}
</style>



